import { useCallback } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useDispatch } from 'react-redux'
import { updateUserStakedBalance, updateUserBalance } from 'state/actions'
import { stake, sousStake, restake } from 'utils/callHelpers'
import { useMasterchef, useSousChef } from './useContract'
import { fetchFarmUserDataAsync } from '../state/farms'

export const useStake = (pid: number) => {
  const dispatch = useDispatch()
  const { account } = useWallet()
  const masterChefContract = useMasterchef()

  const handleStake = useCallback(
    async (amount: string) => {
      const txHash = await stake(masterChefContract, pid, amount, account)
      dispatch(fetchFarmUserDataAsync(account))
      console.info(txHash)
    },
    [account, dispatch, masterChefContract, pid],
  )

  return { onStake: handleStake }
}
export const useReStake = (pid: number) => {
  const dispatch = useDispatch()
  const { account } = useWallet()
  const masterChefContract = useMasterchef()

  const handleStake = useCallback(async () => {
    const txHash = await restake(masterChefContract, pid, account)
    dispatch(fetchFarmUserDataAsync(account))
    console.info(txHash)
  }, [account, dispatch, masterChefContract, pid])

  return { onStake: handleStake }
}

export const useSousStake = (sousId) => {
  const dispatch = useDispatch()
  const { account } = useWallet()
  const sousChefContract = useSousChef(sousId)

  const handleStake = useCallback(
    async (amount: string) => {
      await sousStake(sousChefContract, amount, account)
      dispatch(updateUserStakedBalance(sousId, account))
      dispatch(updateUserBalance(sousId, account))
    },
    [account, dispatch, sousChefContract, sousId],
  )

  return { onStake: handleStake }
}

export default useStake
